	background-image: url('/img/levels/s1-1.jpg');
} */

#inner-screen {
	display: -webkit-flex;
	display: flex;
	height: 100%;
	width: 100%;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

#inner-screen img{
	width:50%;
	/* margin-top: -200px; */
	margin-bottom: 7%;
}
.btn:not(:disabled){
    cursor: pointer;
}

.start-btn, .btn{
    width:100%;
    padding: 10px;
    color: lightgreen;
    background-color: gray;
    opacity: 0.85;
    background-image: none;
    border-color: #343a40;
	text-shadow: 1px 3px black;
    font-weight: 800;
    -webkit-transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}
.btn {
	font-size: 30px;
}
.start-btn{
    color: lightgreen;
    background-color: gray;
	margin: 10px 0;
	font-size: 2em;
}
.start-btn.credits-btn,
.start-btn.about-btn
{
    color: lightblue;
}

.start-btn:hover{
    color: white;
    background-color: lightgreen;
    opacity: 1;
    background-image: none;
    border-color: #343a40;
    text-shadow: 1px 3px black;
    font-weight: 800;
}

.red-color.active:not(:disabled),
.red-color:hover:not(:disabled) {
    color: #fff;
    background-color: #c82333;
    border-color: #bd2130;
}

.chapter-selector {
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
	-webkit-align-items: center;
	        align-items: center;
	/* background-image: url('/img/levels/s1-1.jpg'); */
}

.chapter {
    font-size: 1.7em;
	width: 45%;
	height: 45%;
	border-radius: 7px;
	display: -webkit-flex;
	display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    cursor: pointer;
    text-align: center;
	opacity: 0.9;
	
	background-size: cover;
	background-position: bottom;
}
.chapter:hover{
    box-shadow: 0 0 10px lightblue;
    opacity: 1;
}
.dialog{
    background-image: url('/img/dialouge.png') !important;
    /* padding: 1% 12% 18% 13%; */
    padding: 1em 3em 5em 3em !important;
    background-size: cover!important;
	background-repeat: round!important;
	font-size: 26px;
	line-height: normal;
}

.dialog-box {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	justify-items: center;
}
#score-board {
	display: -webkit-flex;
	display: flex;
	background: rgba(255, 255, 255, 0.85);
	width: 100%;
	height: 60px;
	position: absolute;
    top: 0;
}

#menu {
	padding: 10px;
	-webkit-flex: 0 0 20%;
	        flex: 0 0 20%;
}

#menu img {
	max-width: 100%;
	height: 100%;
	cursor: pointer;
}

#stage {
	-webkit-flex: 0 0 60%;
	        flex: 0 0 60%;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

#score {
	display: -webkit-flex;
	display: flex;
	-webkit-flex: 0 0 20%;
	        flex: 0 0 20%;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

#score-text {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-basis: 100%;
	        flex-basis: 100%;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

#score-num {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-basis: 100%;
	        flex-basis: 100%;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}
#game {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    overflow: hidden;
    
    -webkit-justify-content: space-evenly;
    
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    border: 1px solid #000000;
    
    background-size: cover;
    background-color: aqua;
    background-repeat: round;
    background-position: bottom;
}
.dimmer{
    display: -webkit-flex !important;
    display: flex !important;
    position: absolute;
    top: 0 !important;
    left: 0 !important;
    width: 100%;
    height: 100%;
    text-align: center;
    vertical-align: middle;
    padding: 1em;
    background-color: rgba(0,0,0,.50);
    opacity: 1;
    line-height: 1;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation-duration: .5s;
    animation-duration: .5s;
    -webkit-transition: background-color .5s linear;
    transition: background-color .5s linear;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    will-change: opacity;
    z-index: 1000;
}
.modal {
    display: block;
    visibility: visible;

    width: 88%;
    min-height: 300px;
    margin: 0;
    position: absolute;

    background: #fff;
    border: none;
    box-shadow: 1px 3px 3px 0 rgba(0,0,0,.2),1px 3px 15px 2px rgba(0,0,0,.2);
    -webkit-transform-origin: 50% 25%;
    transform-origin: 50% 25%;
    -webkit-flex: 0 0 auto;
            flex: 0 0 auto;
    border-radius: .28571429rem;
    -webkit-user-select: text;
    -moz-user-select: text;
    -ms-user-select: text;
    user-select: text;
    will-change: top,left,margin,transform,opacity;
    
    z-index: 1001;
}

.modal-main {
    position:fixed;
    background: white;
    width: 80%;
    height: auto;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}

.display-block {
    display: block;
}

.display-none {
    display: none;
}
